// /*    */

.overlay {
  /*    */
  opacity: 0;
  visibility: hidden;
  overflow-y: scroll;

  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 20;
  transition: 1s all;
}

/*     */

.modal {
  /*    */
  opacity: 0;
  visibility: hidden;
  overflow-y: scroll;

  /*      */
  width: 100%;
  // max-width: 500px;

  /*     z-index */
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 30; /*      */

  /*      */
  box-shadow: 0 3px 10px -0.5px rgba(0, 0, 0, 0.2);
  text-align: center;
  padding: 30px;
  border-radius: 3px;
  background-color: #fff;
  transition: 1s all;
}

/*        */

.modal.active,
.overlay.active {
  opacity: 1;
  visibility: visible;
}

/*     */

.modal__cross {
  width: 15px;
  height: 15px;
  position: absolute;
  top: 20px;
  right: 20px;
  fill: #444;
  cursor: pointer;
}

