.modal-nutrition {
  height: 100%;
  max-width: 568px;
  padding: 32px 40px;
  border-radius: 10px;
  border: 2px solid #f8d24e;
}

.modal__title {
  margin-bottom: 40px;

  font-family: 'Seymour One';
  font-size: 20px;
  line-height: 1.4;

  text-align: center;

  color: $focus-color-text;

  background: linear-gradient(
    to right,
    hsla(0, 0%, 30%, 0.651) 0,
    hsla(0, 0%, 100%, 0) 10%,
    hsl(0, 0%, 30%) 20%
  );
  background-clip: text;
  color: transparent;
  animation: shine 6s infinite linear;
}

@keyframes shine {
  0% {
    background-position: 0;
  }
  60% {
    background-position: 600px;
  }
  100% {
    background-position: 600px;
  }
}

.field-group__input {
  margin-bottom: 16px;
  width: 100%;
  min-height: 42px;
  padding-left: 16px;
  background: rgba(248, 210, 78, 0.1);
  border: 1px solid #84a178;
  border-radius: 10px;
  transition: width 2s;
}

.field-group__input:focus {
  @include mobile {
    width: 350px;
    height: 45px;
  }

  @include tablet {
    width: 395px;
  }

  @include desktop {
    width: 400px;
  }
}

.field-group__input::placeholder {
  font-size: 14px;

  color: $primary-text-color;
}

.field-group__textarea::placeholder {
  font-size: 14px;

  color: $primary-text-color;
}

.field-group__textarea {
  margin-bottom: 32px;
  display: block;
  width: 100%;
  height: 84px;
  padding-left: 16px;
  background: rgba(248, 210, 78, 0.1);
  border: 1px solid #84a178;
  border-radius: 10px;
}

.field-group__send-button {
  margin-left: auto;
  margin-right: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 121px;
  height: 42px;
  font-weight: 600;
  font-size: 14px;
  background-color: #f8d24e;
  border-radius: 10px;
  border: none;
  cursor: pointer;

  transition: width 2s, height 2s, transform 2s;

  &:hover {
    background: $bg-green-button;
    color: $primary-text-color;
    // transition-property: background, color;
    // transition-duration: 300ms;
    // transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transform: rotate(360deg);
  }
}

.form-modal-2 {
  max-width: 368px;
  margin-left: auto;
  margin-right: auto;
}

.modal-picture__wrap {
  position: relative;
  width: 132px;
  height: 139px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 24px;
}

.back-shadow {
  position: absolute;
  top: 7px;
  width: 132px;
  height: 132px;
  background: linear-gradient(
      0deg,
      rgba(156, 190, 143, 0.7),
      rgba(156, 190, 143, 0.7)
    ),
    #d9d9d9;
  box-shadow: 0px 5.26027px 19.726px rgba(10, 136, 23, 0.25);
  border-radius: 50%;
}

.ingredient-title {
  margin-bottom: 24px;
  font-weight: 500;
  font-size: 20px;
  line-height: 1.4;
  color: #131313;
}

.modal-picture {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 20;
}

.nutrition {
  margin-bottom: 48px;
}

.nutrition-ingredients {
  margin-bottom: 11px;
  display: flex;
  justify-content: space-between;
  font-size: 14px;
  line-height: 1.4;
  color: #131313;
}

.nutrition-facts {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-bottom: 48px;
}

.nutrition-facts__list {
  width: 65px;
  height: 90px;
  border: 2px solid #9cbe8f;
  border-radius: 40px;
}

.nutrition-facts__list-weight {
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 6px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 500;
  font-size: 12px;
  line-height: 2.3;
  width: 53px;
  height: 53px;
  border-radius: 50px;
  background: #9cbe8f;
  border: 2px solid #9cbe8f;
  box-shadow: 0px 6px 20px rgba(10, 136, 23, 0.25);
}

.nutrition-facts__list-measure {
  font-size: 12px;
  line-height: 1.3;
  color: #131313;
}
